import Vue from 'vue';
import VueRouter from 'vue-router';                               
import instaBody from '../components/instaBody.vue';                       
import instaPost from '../components/instaPost.vue';                       

Vue.use(VueRouter);                                               
export const router = new VueRouter({                             
    mode: 'history',                                              
    routes:[                                                      
        //path : url 주소, component : 페이지에 보여질 컴포넌트
        {                                                         
            path: '/',
            redirect: '/body'
        },
        {
            path: '/home',
            name: 'home',
            component: instaBody,
        },
        {
            path: '/post',
            name: 'post',
            component: instaPost,
        }
    ]
});